.about {
    background-color: #fff;
    .main-area {
        display: flex;
        .about-left {
            width: 35%;
            padding: 10px;
            margin-right: 120px;
            border-radius: $border_radius;
            background-color: #fff;
            box-shadow: 0px 30px 60px 0px #aaa;
            opacity: 0;
            transform: translateY(30px);
            transition: 1s;
            &.active {
                opacity: 1;
                transform: translateY(0);
            }
            img {
                border-radius: $border_radius;
                width: 100%;
                height: 100%;
            }
        }
        .about-right {
            width: 50%;
            .about-name {
                margin-top: 15px;
                font-size: 30px;
                font-weight: 800;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 0.5s;
                &.active {
                    opacity: 1;
                }
            }
            .about-position {
                font-size: 16px;
                display: flex;
                margin: 5px 0 0;
                white-space: nowrap;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 1s;
                &.active {
                    opacity: 1;
                }
                div {
                    margin-right: 10px;
                }
            }
            .about-intro {
                margin: 25px 0;
                text-indent: 20px;
                font-size: 13px;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 1.5s;
                &.active {
                    opacity: 1;
                }
            }
            .about-self-info {
                display: flex;
                flex-direction: column;
                margin-bottom: 40px;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 2s;
                &.active {
                    opacity: 1;
                }
                .info {
                    display: flex;
                    margin: 5px 0;
                    .info-label {
                        font-weight: 600;
                        margin: 0;
                        color: #090a0c;
                        font-size: 14px;
                        border-right: 1px solid #c1c1c1;
                        width: 70px;
                    }
                    .info-value {
                        font-size: 14px;
                        font-weight: 400;
                        margin-left: 10px;
                    }
                }
            }
            .about-downloadCV {
                width: 140px;
                font-size: 14px;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 2.5s;
                &.active {
                    opacity: 1;
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    .about {
        .main-area {
            .about-left {
                width: 45%;
                margin-right: 60px;
            }
            .about-right {
                width: 50%;
                .about-name {
                    font-size: 26px;
                }
                .about-position {
                    font-size: 14px;
                }
                .about-intro {
                    font-size: 12px;
                }
                .about-self-info {
                    .info {
                        .info-label {
                            font-size: 13px;
                            width: 65px;
                        }
                        .info-value {
                            font-size: 13px;
                        }
                    }
                }
                .about-downloadCV {
                    font-size: 12px;
                }
            }
        }
    }
}

@media (max-width: 1000px) {
    .about {
        .main-area {
            .about-left {
                width: 45%;
                height: 270px;
            }
            .about-right {
                width: 50%;
                .about-name {
                    font-size: 20px;
                }
                .about-position {
                    font-size: 12px;
                }
                .about-self-info {
                    .info {
                        .info-label {
                            font-size: 12px;
                            width: 60px;
                        }
                        .info-value {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 880px) {
    .about {
        .main-area {
            flex-direction: column;
            align-items: center;
            .about-left {
                height: auto;
                width: 100%;
                margin-right: 0;
                margin-bottom: 30px;
            }
            .about-right {
                width: 100%;
                padding: 0 $padding_md 0;
                .about-name {
                    font-size: 20px;
                }
            }
        }
    }
}

@media (max-width: 550px) {
    .about {
        .main-area {
            .about-left {
                width: 100%;
            }
            .about-right {
                padding: 0 $padding_xs 0;
                .about-name {
                    font-size: 16px;
                }
            }
        }
    }
}
