.project {
    background-color: $second_bg;
    background-image: linear-gradient(
        -60deg,
        #dcdddd 0%,
        $primary_bg_light 100%
    );
    .title-inner {
        color: #fff;
        &::after {
            background: $orange_bg;
        }
    }
    .subtitle {
        color: $light_bg;
    }
    .main-area {
        .project-swiper {
            opacity: 0;
            transition: 2.5s;
            &.active {
                opacity: 1;
            }
        }
        .project-item {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            height: 195px;
            border-radius: 25px;
            background-image: linear-gradient(to top, $orange_bg, $light_bg);
            .badge {
                z-index: 10;
                position: absolute;
                top: 25px;
                left: 0;
                background-color: $orange_bg_dark;
                color: #fff;
                font-size: 16px;
                box-shadow: 1px 0 5px #999;
                padding: 2px 10px;
                border-radius: 0 5px 5px 0;
                cursor: pointer;
            }
            .rwd-text {
                display: none;
            }
            .project-img {
                transform: translateX(0px) translateY(-25px);
                height: 90%;
                width: 87%;
                border-radius: 20px;
                position: relative;
                .img-self {
                    height: 100%;
                    width: 100%;
                    border-radius: $border_radius;
                    background-position: center;
                    background-size: cover;
                    background-repeat: no-repeat;
                    box-shadow: 0 5px 10px rgba(15, 53, 129, 0.5);
                }
            }
            .project-info {
                cursor: pointer;
                border-radius: $border_radius;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: rgba(15, 53, 129, 0.7);
                opacity: 0;
                transition: 0.5s;
                color: #fff;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                text-align: center;
                font-size: 14px;
                .project-text {
                    font-weight: 600;
                    margin-bottom: 10px;
                }
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}
@media (max-width: 1736px) {
    .project {
        .main-area {
            .project-item {
                height: 180px;
            }
        }
    }
}
@media (max-width: 1536px) {
    .project {
        .main-area {
            .project-item {
                height: 170px;
                .project-info {
                    font-size: 12px;
                }
            }
        }
    }
}

