.bottom-top {
    padding: 106px 0 111px;
    background-size: cover;
    background-position: center center;
    position: relative;
    background-image: linear-gradient(0deg, #7992d3 0%, #1b3f7e 100%);
    overflow: hidden;
    .bg-shape {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        .shape-left {
            z-index: 1;
            top: 0;
            left: 0;
        }
        .shape-right {
            z-index: 1;
            bottom: 0;
            right: 0;
        }
        img {
            max-width: 100%;
            height: auto;
            vertical-align: middle;
            border-style: none;
            position: absolute;
        }
    }
    .bottom-top-inner {
        padding: 0 50px;
        display: flex;
        .left-area {
            z-index: 2;
            width: 80%;
        }
        .right-area {
            z-index: 2;
            width: 20%;
        }
    }
}

.bottomTop-heading {
    z-index: 19;
    .title {
        font-size: 50px;
        line-height: 64px;
        color: #fff;
        font-weight: 700;
        margin-bottom: 20px;
    }
    p {
        color: #fff;
        margin: 0;
    }
}

.pixFadeUp {
    visibility: visible;
    animation-name: pixFadeUp;
}

@media (max-width: 1200px) {
    .bottom-top {
        .bottom-top-inner {
            padding: 0 200px 0 100px;
        }
    }
    .bottomTop-heading {
        .title {
            font-size: 35px !important;
            line-height: 49px;
            margin-bottom: 10px;
        }
        p {
            font-size: 14px;
        }
    }
}
@media (max-width: 585px) {
    .bottom-top {
        .bottom-top-inner {
            flex-direction: column;
            align-items: center !important;
            padding: 0;
            .left-area {
                display: flex;
                justify-content: center;
                width: 100%;
            }
            .right-area {
                display: flex;
                justify-content: center;
                width: 100%;
                margin-top: 50px;
            }
        }
    }
    .bottomTop-heading {
        padding: 0 50px;
        .title {
            font-size: 35px !important;
            line-height: 49px;
            margin-bottom: 10px;
        }
        p {
            font-size: 14px;
        }
    }
}
