.bottom {
    display: flex;
    justify-content: space-between;
    background-color: $primary_text;
    color: #fff;
    padding: 10px 30px;
    font-size: 12px;
    .bottom-logo {
        width: 20px;
        height: 20px;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .bottom-logo-rwd {
        display: none;
    }

    .bottom-copyright-rwd {
        display: none;
    }
    .bottom-cooperator {
        margin-right: 40px;
        a {
            color: #fff;
        }
    }
}

@media (max-width: 700px) {
    .bottom {
        .bottom-logo {
            display: none;
        }
        .bottom-logo-rwd {
            display: block;
            width: 25px;
            height: 25px;
            margin-bottom: 5px;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .bottom-copyright {
            display: none;
        }
        .bottom-copyright-rwd {
            display: block;
            white-space: nowrap;
        }
        .bottom-cooperator {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin-right: 0;
        }
    }
}
