.contact {
    background-color: #fff;
    opacity: 0;
    transform: translateY(-100px);
    transition: 1s;
    &.active {
        opacity: 1;
        transform: translateY(0);
    }
    .main-area {
        box-shadow: 2px 2px 10px #aaa;
        border-radius: $border_radius;
        .map {
            width: 100%;
            img {
                vertical-align: top;
                width: 100%;
                height: 100%;
                border-radius: $border_radius $border_radius 0 0;
            }
        }
        .contact-bottom {
            display: flex;
            background-color: #fff;
            .contact-content {
                padding: 50px;
                background-color: #fff;
                border-radius: 0 0 $border_radius $border_radius;
                font-size: 14px;
                width: 50%;
                .contact-title {
                    font-weight: 600;
                    display: flex;
                    justify-content: center;
                    font-size: 20px;
                    margin-bottom: 30px;
                }
                .icon {
                    color: $primary_text;
                    svg {
                        font-size: 40px;
                    }
                }
                .name {
                    font-weight: 600;
                    margin: 10px 0;
                    font-size: 16px;
                }
                .value {
                    color: #777;
                    font-size: 12px;
                }
                .address {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    text-align: center;
                    margin-bottom: 80px;
                    margin-top: 50px;
                }
                .phone-email {
                    display: flex;
                    align-items: center;
                    margin-top: 70px;
                    .phone {
                        width: 50%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        text-align: center;
                    }
                    .email {
                        width: 50%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        text-align: center;
                    }
                }
            }
            .survey {
                background-color: #fff;
                padding: 50px 50px 50px 0;
                width: 50%;
                .contact-title {
                    font-weight: 600;
                    display: flex;
                    justify-content: center;
                    margin-bottom: 30px;
                    font-size: 20px;
                }
                .name-phone {
                    display: flex;
                    margin-bottom: 10px;
                    width: 100%;
                    .name {
                        margin-right: 10px;
                        width: 100%;
                        .MuiFormControl-root {
                            width: 100%;
                            .MuiOutlinedInput-root {
                                border-radius: $border_radius;
                                input {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                    .phone {
                        width: 100%;
                        .MuiFormControl-root {
                            width: 100%;
                            .MuiOutlinedInput-root {
                                border-radius: $border_radius;
                                input {
                                    font-size: 14px;
                                    &::-webkit-outer-spin-button,
                                    &::-webkit-inner-spin-button {
                                        -webkit-appearance: none;
                                        margin: 0;
                                    }
                                    &[type="number"] {
                                        -moz-appearance: textfield;
                                    }
                                }
                            }
                        }
                    }
                }
                .email {
                    width: 100%;
                    margin-bottom: 10px;
                    .MuiFormControl-root {
                        width: 100%;
                        .MuiOutlinedInput-root {
                            border-radius: $border_radius;
                            input {
                                font-size: 14px;
                            }
                        }
                    }
                }
                .content {
                    width: 100%;
                    .MuiFormControl-root {
                        width: 100%;
                        textarea {
                            border-radius: $border_radius;
                            resize: none;
                            border: 1px solid #bbb;
                            padding: 10px;
                            font-size: 14px;
                            &::placeholder {
                                font-size: 16px;
                            }
                        }
                    }
                }
                .btn {
                    margin-top: 10px;
                    width: 100%;
                }
            }
        }
    }
}

@media (max-width: 1000px) {
    .contact {
        .main-area {
            .contact-bottom {
                .contact-content {
                    padding: 50px 20px;
                }
                .survey {
                    padding: 50px 30px 50px 0;
                }
            }
        }
    }
}
@media (max-width: 900px) {
    .contact {
        .main-area {
            .contact-bottom {
                flex-direction: column;
                align-items: center;
                .contact-content {
                    width: 100%;
                    padding: 50px 0 20px;
                    .address {
                        margin: 40px 0 10px;
                    }
                    .phone-email {
                        margin: 50px 0 0;
                    }
                }
                .survey {
                    width: 80%;
                    padding: 30px 0;
                }
            }
        }
    }
}
@media (max-width: 700px) {
    .contact {
        .main-area {
            .contact-bottom {
                .contact-content {
                    .value {
                        transform: scale(0.9) !important;
                    }
                }
                .survey {
                    width: 90%;
                }
            }
        }
    }
}
